<template>
  <div  class="sideBarHeight col-md-2 text-center px-0">
    <i class="fas fa-home fa-2x text-white mt-4"></i>
    <p class="font-weight-bold text-white mb-8">SHOP</p>
    <ul class="d-flex flex-column align-items-center pl-0">
      <li class="d-flex flex-column align-items-center justify-content-center" 
      :class="{'bg-secondary':functionIcon == 'product'}"
      @click="functionIcon = 'product'"
      >
        <img src="../assets/img/icons8-boarding-pass-96.png" alt="">
      </li>
      <p class="text-light mt-2">商品管理</p>
      <li class="d-flex flex-column align-items-center justify-content-center"
      :class="{'bg-secondary':functionIcon == 'order'}"
      @click="functionIcon = 'order'">
        <img src="../assets/img/icons8-purchase-order-90.png" alt="">
      </li>
      <p class="text-light mt-2">訂單管理</p>
      <li  class="d-flex flex-column align-items-center justify-content-center"
      :class="{'bg-secondary':functionIcon == 'myShop'}"
      @click="functionIcon = 'myShop'"
      >
        <img src="../assets/img/icons8-shop-100.png" alt="">
      </li>
      <p class="text-light mt-2">我的店家</p>
      <li  class="d-flex flex-column align-items-center justify-content-center"
      :class="{'bg-secondary':functionIcon == 'setting'}"
      @click="functionIcon = 'setting'"
      >
        <i class="fas fa-cog fa-2x text-light"></i>
      </li>
      <p class="text-light mt-2">設定</p>
      <li  class="d-flex flex-column align-items-center justify-content-center"
      :class="{'bg-secondary':functionIcon == 'cardPay'}"
      @click="functionIcon = 'cardPay'"
      >
        <img src="../assets/img/icons8-buy-with-card-96.png" alt="">
      </li>
      <p class="text-light mt-2">信託請款</p>
    </ul>
  </div>
</template>
<script>
export default {
  data(){
    return{
      functionIcon : 'product'
    }
  }
}
</script>
<style lang="scss">
.sideBarHeight{
  height: 100vh;
  max-width: 127px;
  overflow-y: scroll;
  background: transparent linear-gradient(1deg, #3B579D 0%, #3B44A9 100%) 0% 0% no-repeat padding-box;
  &::-webkit-scrollbar {
    display: none;
  }
}
  ul{
    list-style :none;
    &>li{
      width: 68px;
      height: 68px;
      border-radius: 10px;
      background: rgba(0,0,0,.12);
      cursor: pointer;
      &:hover{
        background: rgba(0,0,0,.64);
      }
      img{
        width: 32px;
        height: 32px;
        cursor: pointer;
      }
    }
    p{
      font-family: 'Noto Sans TC', sans-serif;
      margin-bottom: 27px;
    }
  }
</style>


